.container {
  //breakpoints:
  //mobile - 480px
  //tablet - 768px
  //desktop - 1200px

  padding-left: 16px;
  padding-right: 16px;
  width: 100%;

  margin: 0 auto;

  @include mobile {
    max-width: 480px;
  }

  // @include mobile {
  //   width: 480px;
  // }

  //tablet-style

  @include tablet {
    max-width: 768px;
    padding-left: 40px;
    padding-right: 40px;
  }

  //desktop-style

  @include desktop {
    min-width: 1280px;
  }
}
